import React, { useEffect, useCallback, useState} from 'react';
import CloseIcon from 'components/svg/CloseIcon.js';
import {useTokenOwners} from 'utils/zorbs.js';
import ENSName from 'components/ens/ENSName.js';
import Clock from './Clock.js';
import {getZporeRemixes} from 'zpore/getZporeRemixes.js';
import {connect} from 'react-redux';
import CurveDiv from 'components/ux/CurveDiv.js';
import './header.scss';
import SimpleControls from 'components/player/SimpleControls.js';
import {walk, curve, relative, cut, radius} from 'components/ux/dsl.js';

const ZporeControlHeader = ({
    hideLeaks,
    nextLeak,
    chooseLeak,
    artist,
    songId,
    title,
    playerRef,
    setShowRemixes,
    setRemixes,
    remixes
}) => { 

    useEffect(() => {
        getZporeRemixes(songId)
            .then(result => {
                setRemixes(result);
            });
    }, [songId]);

    let {zorbs} = useTokenOwners({account: {address: nextLeak && nextLeak.mintInfo.originatorAddress}});

    const DROP_ALGO = [
        [],
        [
            walk(140),
            curve()
                .start(10)
                .end(30)
                .tension(10)
                .depth(40),
            walk(30),
            curve()
                .start(10)
                .end(50)
                .tension(30)
                .depth(-20)
        ],
        [
            walk(35),
            curve()
                .start(10)
                .end(50)
                .tension(30)
                .depth(-40)
        ],
        [walk(5),
        ]
    ];

    let walkAmount = 160; //nextLeak ? 200 + 2*(nextLeak.song.title + nextLeak.song.artist).length : 10;
    const DROP_ALGO_2 = [
        [],
        [
            walk(Math.min(20, walkAmount)),
            curve()
                .start(15)
                .end(80)
                .tension(60)
                .depth(18),
            walk(30),
            curve()
                .start(10)
                .end(30)
                .tension(20)
                .depth(-18)
        ],
        [
        ],
        [
        ]
    ];

    let leakCreator = nextLeak && nextLeak.mintInfo.originatorAddress;

    let zorb = zorbs && zorbs[0] && zorbs[0].image.url;

    let dateLeaked = nextLeak && new Date(nextLeak.mintInfo.mintContext.blockTimestamp);

    let timeElapsed = dateLeaked && new Date().getTime() - dateLeaked.getTime();

    return [
        (<CurveDiv
           radius={12}
           className={"zpore-control-header next-leak-controller " + (nextLeak ? "" : " invisible")}
           algo={DROP_ALGO_2}
           fill="#5e008082"
           stroke="#2e2c2c">
           <div className="flex z-30 flex-col  overflow-hidden inner-control">
             <div className="flex items-start">
               <div className="flex-col flex">
                 <div className="w-full pl-1 pt-3 pb-2 flex text-xs top-header">
                   <CloseIcon
                     className="close-btn z-30 w-4 h-5 pointer-events-auto" viewPort="0 0 12 12" version="1.1"
                     onClick={() => hideLeaks(15000)}
                   />
                   {zorb && <img className="zorb-multiplayer ml-3 w-4 h-4" src={zorb}/>} <ENSName className="mx-2" address={leakCreator}/> <span className="italic">wants to play</span>
                 </div>
                 <div className="px-3 pt-1 pb-3 flex">
                   {playerRef && <SimpleControls playerRef={playerRef}/>}
                   <Clock className="text-xs"/>
                   <span className="text-xs">
                     <span className="italic ml-2">leaked</span> {leakTime(timeElapsed)} ago
                   </span>
                 </div>
               </div>
               <div
                 onClick={() => chooseLeak()}
                 className="pushable flex items-start text-s ml-auto mr-1 my-auto px-3 py-3 load-into-spore bg-black text-white">
                 <div className="w-24">play multiplayer</div> <img className="w-8 h-8 mx-2 my-auto zorb-multiplayer" src={zorb}/>
               </div>
             </div>
           </div>
         </CurveDiv>),
         (<CurveDiv radius={12} className={"zpore-control-header " + (nextLeak ? " invisible" : "")} algo={DROP_ALGO} fill="black" stroke="#2e2c2c">
           <div className="flex z-20 flex-col  overflow-hidden inner-control">
             <div className="w-full px-3 py-2 flex text-sm top-header">
               <div>{title}</div> <div className="ml-2 text-slate-500 underline decoration-solid">{artist}</div>
             </div>
             <div className="w-full p-3 flex">
               <div className="flex">
                 {playerRef && <SimpleControls playerRef={playerRef}/>}
                 <Clock/>
               </div>
               <div onClick={() => setShowRemixes(true)} className="ml-auto remixes-btn text-sm">
                 {remixes.length} remixes 
               </div>
             </div>
           </div>
         </CurveDiv>)
    ];
};

const leakTime  = (time) => {
    let minutes = time / 1000 / 60;
    if (minutes < 60) {
        return Math.ceil(minutes) + " min. ";
    }

    let hours = minutes / 60;
    if (hours < 48) {
        return Math.ceil(hours) + " hours "
    }

    let days = hours / 24;
    return Math.round(days) + " days ";
};

const mapStateToProps = (state, props) => {
  return {
      playerRef: state.spore.playerRef
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZporeControlHeader);
